<template>
  <v-container class="pa-5">
    <div><span class="text-h5 font-weight-bold">My Coupon</span></div>
    <div>
      <v-tabs active-class="active-tab" slider-color="black" v-model="tab">
        <v-tab>보유한 쿠폰</v-tab>
        <v-tab>사용내역</v-tab>
      </v-tabs>

      <v-tabs-items v-model="tab">
        <v-tab-item>
          <v-row class="grey lighten-4 pt-3">
            <v-col class="pa-5">
              <v-card v-if="loading">로딩 중 </v-card>
              <v-card v-for="coupon in couponList" :key="coupon.mcno" shaped class="mb-4">
                <v-card-title class="font-weight-bold pb-1">{{ coupon.couname }}</v-card-title>
                <v-card-text class="pb-0">
                  유효기간 : {{ new Date(coupon.coustartdate).toLocaleString().substring(0, 12) }} ~
                  {{ new Date(coupon.mcenddate).toLocaleString().substring(0, 12) }}
                </v-card-text>
                <v-card-text class="pt-1 pb-1"><span v-html="coupon.coucontent"></span></v-card-text>
                <v-card-text class="pt-1 pb-1 font-weight-regular text-h5 black--text">{{ coupon.coubenefit }}% OFF</v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </v-tab-item>

        <v-tab-item>
          <v-row class="grey lighten-2 pt-3">
            <v-col class="pa-5">
              <v-card v-for="coupon in usedLog" :key="coupon.mcno" shaped class="mb-4" disabled>
                <v-card-title class="font-weight-bold pb-1">{{ coupon.couname }}</v-card-title>
                <v-card-text class="pb-0">
                  유효기간 : {{ new Date(coupon.coustartdate).toLocaleString().substring(0, 12) }} ~
                  {{ new Date(coupon.mcenddate).toLocaleString().substring(0, 12) }}
                </v-card-text>
                <v-card-text class="pt-1 pb-1">{{ coupon.coucontent }}</v-card-text>
                <v-card-text class="pt-1 pb-1 font-weight-regular text-h5 black--text">{{ coupon.coubenefit }} OFF</v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </v-tab-item>
      </v-tabs-items>
    </div>
  </v-container>
</template>

<script>
import MemberApi from "@/apis/member";
export default {
  name: "Coupon",
  data: () => ({
    couponList: [],
    usedLog: [],
    tab: null,
    loading: false,
  }),
  methods: {
    async getCoupon(mno) {
      try {
        this.loading = true;
        let coupon = await MemberApi.allCoupon(mno);
        this.loading = false;
        this.couponList = coupon.data.coupons;
        this.usedLog = coupon.data.usedCoupons;
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getCoupon(this.$store.getters.getMno);
  },
};
</script>

<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
}
.fixed-tabs-bar .v-tabs__bar {
  position: -webkit-sticky;
  position: sticky;
  top: 4rem;
  z-index: 2;
}
</style>
